const fixBlocksHeight = () => {
  fixBlocks.forEach((block) => {
    const vh = window.innerHeight;
    block.style.height = `${vh}px`;
  });
};

const removeBlocksHeight = () => {
  fixBlocks.forEach((block) => {
    block.style.height = 'inherit';
  });
};

const breakpointChecker = () => {
  if (breakpoint.matches) {
    isMobile = true;
    fixBlocksHeight();
  } else {
    isMobile = false;
    removeBlocksHeight();
  }
};

export {initVhFix};
