import {iosAndroidVhFix} from './utils/ios-android-vh-fix';
import {initModals} from './modules/modals/init-modals';
import {initSlider} from './modules/main-slider';
import {initMainPage} from './modules/main-page';
import {initPreloader} from './modules/init-preloader';
import {initAnimPage} from './modules/init-anim-page';
import {initScrollSlider} from './modules/scroll-slider';
import {initRedirect} from './modules/init-redirect';
import {initParallax} from './modules/init-parallax';
import {getDeviceInfo} from './modules/get-device-info';
import {initVhFix} from './modules/mobile-vh-fix';

// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  // Utils
  // ---------------------------------

  // только если ie11
  // ieFix();
  // https://github.com/fregante/object-fit-images
  // polyfillObjectFit();

  iosAndroidVhFix();
  initSlider();
  initMainPage();
  initRedirect();
  initParallax();
  getDeviceInfo();
  initVhFix();

  // Modules
  // ---------------------------------

  // все скрипты должны быть в обработчике 'DOMContentLoaded', но не все в 'load'
  // в load следует добавить скрипты, не участвующие в работе первого экрана
  window.addEventListener('load', () => {
    initModals();
    initPreloader();
    initAnimPage();
    initScrollSlider();
  });
});

